<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process256"></a>

<p><br></p>
<p><span class="rvts17">章节探索</span><span class="rvts50"> </span><span class="rvts17">阿克斯</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts21">阿克斯任意地点</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">探索阿克斯几处重要的地点。</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1403.jpg" display="block"><span class="rvts20"></span>
<br><span class="rvts21">到达阿克斯之后，主要任务是要追寻大锤达利斯，找出她背后的阴谋是什么。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1404.jpg" display="block"><span class="rvts20"></span>
<br><span class="rvts21">在未进入阿克斯城市之前，可以发现地图东边的港口已经被虚空虫所占领。击败这里的散落泥流怪，可以在一处沉船附近找到一个宝箱，里面有一些不错的补给和装备。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1405.jpg" display="block"><span class="rvts20"></span>
<br><span class="rvts21">在阿克斯一个最为重要的任务地点就是兵营监狱，它位于</span><span class="rvts20">X:319,Y:243</span><span class="rvts21">的位置。兵营监狱里面有一条密道，可以通往地下道。</span>
<br><span class="rvts21">在兵营中，可以接到</span><span class="rvts20"></span><span class="rvts21">失踪的囚犯</span><span class="rvts20"></span><span class="rvts21">，以及发现它其实与地下道相互连接。</span><span class="rvts20"></span><span class="rvts21">曾经的错误</span><span class="rvts20"></span><span class="rvts21">，</span><span class="rvts20"></span><span class="rvts21">怜悯就是力量</span><span class="rvts20"></span><span class="rvts21">支线也在其中。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1406.jpg" display="block"><span class="rvts20"></span>
<br><span class="rvts21">地下道的入口非常的隐秘，并且只有这一个，位于兵营海岸边的一个死亡魔导师的尸体旁。位置</span><span class="rvts20">X:377,Y:233</span><span class="rvts21">。</span>
<br><span class="rvts21">地下道中可以找到</span><span class="rvts20"></span><span class="rvts21">矮人的秘密</span><span class="rvts20"></span><span class="rvts21">，</span><span class="rvts20"></span><span class="rvts21">失踪的囚犯</span><span class="rvts20"></span><span class="rvts21">支线的任务内容，以及一块可以打开德林</span><span class="rvts20"></span><span class="rvts21">凯姆藏宝室的道具屏风，因此该地点也是一个非常重要的地方。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1407.jpg" display="block"><span class="rvts20"></span>
<br><span class="rvts21">整张阿克斯地图，有一处可以无限补充源力的地方，这个地方也是整张地图的一个核心地点。它就是位于林德</span><span class="rvts20"></span><span class="rvts21">凯姆藏宝室深处，阿虎大人被俘的地方。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1408.jpg" display="block"><span class="rvts20"></span>
<br><span class="rvts21">另外，整张地图可以找到一把相当有用的任务道具：破誓者。它也位于林德</span><span class="rvts20"></span><span class="rvts21">凯姆藏宝室阿虎大人被俘的地方。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1409.jpg" display="block"><span class="rvts20"></span>
<br><span class="rvts21">德林</span><span class="rvts20"></span><span class="rvts21">凯姆藏宝室位于凯姆花园中，需要拉动一处隐秘的并且是锁住的闸刀，因此需要较高的智力值（</span><span class="rvts20">Wits</span><span class="rvts21">）和偷盗值（</span><span class="rvts20">Thievery</span><span class="rvts21">）。该位置坐标</span><span class="rvts20">X:146,Y:306</span><span class="rvts21">。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1410.jpg" display="block"><span class="rvts20"></span>
<br><span class="rvts21">打开隐秘舱门后，德林</span><span class="rvts20"></span><span class="rvts21">凯姆藏宝室的入口位于此处。位置坐标</span><span class="rvts20">X:135,Y:285</span><span class="rvts21">。</span>
<br><span class="rvts21">德林</span><span class="rvts20"></span><span class="rvts21">凯姆藏宝室是在阿克斯所有的主支线任务中最为重要的一处地点。其中涉及</span><span class="rvts20"></span><span class="rvts21">德林</span><span class="rvts20"></span><span class="rvts21">凯姆藏宝室</span><span class="rvts20"></span><span class="rvts21">，</span><span class="rvts20"></span><span class="rvts21">寻找阿虎大人</span><span class="rvts20"></span><span class="rvts21">，</span><span class="rvts20"></span><span class="rvts21">一个工艺人的美梦和噩梦</span><span class="rvts20"></span><span class="rvts21">，</span><span class="rvts20"></span><span class="rvts21">大锤的陨落</span><span class="rvts20"></span><span class="rvts21">等支线内容。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1411.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">另外，在德林凯姆的后院，有一个玻璃容器，里面有一朵尖叫之花，吃下它后，可以获得尖叫花的尖叫技能，使附近敌人魔法值降低。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



